import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { Storage } from '@ionic/storage';
import { Store } from '@ngxs/store';
import { IonicComponentLifecycles } from 'src/app/interfaces/component';
import { PlatformService } from 'src/app/services/platform.service';
import { ClearAllStates } from 'src/app/store/actions/global.actions';

@Component({
  selector: 'app-logout',
  styleUrls: ['./logout.page.scss'],
  templateUrl: './logout.page.html',
})
export class LogoutPage implements IonicComponentLifecycles {
  renderWebView: boolean;
  constructor(
    private store: Store,
    private storage: Storage,
    private platform: PlatformService,
    private router: Router
  ) {}

  async ionViewDidEnter() {
    this.renderWebView = this.platform.isWeb();
    this.clearState();
  }

  async clearState() {
    this.store.dispatch(new ClearAllStates());
    await this.storage.clear();
    this.router.navigate(['/', 'login']);
  }

  ionViewWillLeave() {}
}
